*,
*:before,
*:after {
    box-sizing: border-box;
}
* {
    margin: 0;
    padding: 0;
    font: inherit;
}

@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Fleur+De+Leah&family=Great+Vibes&family=Itim&family=Nabla&display=swap");
.title-h1 {
    font-family: "Fleur De Leah", cursive;
    font-size: 60px;
    color: white;
}

.title-top {
    margin: 20px 0;
}

.title-top h1 {
    font-family: "Fleur De Leah", cursive;
    font-size: 50px;
}

.box-bg2 {
    width: 555px;
    height: 555px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title-left {
    font-family: "Great Vibes", cursive;
    font-size: 50px;
    margin-top: 10px;
}

.box-left {
    position: relative;
    font-size: 16px;
    font-size: 1.06667rem;
    font-weight: 300;
    color: #797979;
}
.box-left button {
    background-color: #c89d9c;
    color: #fff;
    padding: 15px 50px 15px 25px;
    font-weight: 600;
    border: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    -o-border-radius: 0;
    -ms-border-radius: 0;

    display: inline-block;
    position: relative;
    border-radius: 8px;
    margin: 28px 0;
    font-family: "Itim", cursive;
}

.nameUser {
    font-size: 1.5rem;
    font-weight: 600;
    margin: 2em 0 1em;
}

.wish {
    text-align: center;
    font-size: 1.3rem;
    margin-bottom: 2em;
    width: 75%;
    font-family: "Itim", cursive;
    margin-top: 1em;
}

th,
td {
    padding: 10px;
    z-index: 20;
}

.today {
    position: relative;
}

.today:before {
    position: absolute;
    top: 5px;
    left: 28px;
    content: "12";
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 20px;
    padding: 5px;
    background-color: rgb(200, 157, 156);
    color: #fff;
    border-radius: 50%;
    z-index: 1;
}

.time-event {
    display: flex;
    flex-direction: column;
    height: 1054px;
    background-color: #f2eded;
    margin-bottom: 50px;
}

.box-event-detail {
    background-image: url("./image/bg2.png");
    width: 273px;
    height: 579px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.box-event-detail img {
    width: 233px;
    height: 323px;
    background-size: contain;
    border-radius: 8px;
}

.title-event {
    font-size: 1.36667rem;
    position: relative;
    margin: 1.4em 0 0.5em;
    padding: 0 0 0.8em;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.1;
}

.title-event::after {
    position: absolute;
    bottom: 0;
    left: 0;
    content: "";
    width: 80%;
    border-bottom: 3px solid gray;
    display: flex;
    margin-left: 10%;
}

.address {
    font-family: "Itim", cursive;
}

.box-videos {
    width: 100%;
    height: 496px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
    background-image: url("./image/DSC_2410.JPG");
    background-size: cover;
    background-position: center;
}
.box-videos h1 {
    font-family: "Dancing Script", cursive;
    font-size: 60px;
    text-align: center;
    font-weight: 500;
    line-height: 66px;
    color: #fff;
}
.box-videos button {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    color: #af7372;
    border: 2px solid rgba(231, 76, 60, 1);
    background-color: rgba(200, 157, 156, 0.5);
}
.box-videos p {
    color: #ffffff;
}

@media only screen and (max-width: 480px) {
    .title-top h1 {
        font-size: 15px;
    }

    .title-h1 {
        font-family: "Fleur De Leah", cursive;
        font-size: 21px;
        color: white;
    }

    .title-h1-sub {
        font-size: 21px !important;
    }

    .bg2 {
        display: flex;
        flex-direction: column;
        background-image: url(http://localhost:3000/static/media/bg2.665c783….png);
        width: 100%;
        height: 1124px;
        justify-content: center;
        align-items: center;
        gap: 20px;
    }

    .box-bg2 {
        width: 400px;
        height: 555px;
        background-color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .table-body {
        border: 5px solid rgb(129 103 103 / 40%);
        padding: 0 !important;
    }

    .mini-calender {
        width: 360px;
    }

    .today:before {
        position: absolute;
        top: 5px;
        left: 12px;
        content: "12";
        display: inline-block;
        width: 30px;
        height: 30px;
        line-height: 20px;
        padding: 5px;
        background-color: rgb(200, 157, 156);
        color: #fff;
        border-radius: 50%;
        z-index: 1;
    }

    .time-event-detail {
        width: 100%;
        height: 609px;
        position: absolute;
        top: 50%;
        transform: translate("-50% , -50%");
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 20px;
        flex-wrap: wrap;
    }

    .box-event-detail {
        background-image: url("./image/bg2.png");
        width: 164px;
        height: 450px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
    }

    .box-event-detail img {
        width: 150px;
        min-height: 163px;
        background-size: contain;
        border-radius: 8px;
    }

    .title-event {
        font-size: 1rem;
        position: relative;
        margin: 1.4em 0 0.5em;
        padding: 0 0 0.8em;
        font-family: inherit;
        font-weight: 500;
        line-height: 1.1;
    }

    .title-event::after {
        position: absolute;
        bottom: 0;
        left: 0;
        content: "";
        width: 80%;
        border-bottom: 3px solid gray;
        display: flex;
        margin-left: 10%;
    }

    .address {
        font-size: 14px !important;
        text-align: center !important;
        margin: 20px 10px !important;
    }

    .bg-event {
        height: 265px;
        background-image: url("./image/DSC_2363.JPG");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        position: relative;
    }

    .time-events {
        font-size: 12px;
    }
}

@media only screen and (min-width: 480px) {
    .time-event-detail {
        width: 100%;
        height: 609px;
        position: absolute;
        top: 50%;
        left: 20%;
        transform: translate("-50% , -50%");
        display: flex;
        justify-content: center;
        align-items: center;
        column-gap: 20px;
        flex-wrap: wrap;
    }

    .bg2 {
        background-image: url("./image/bg2.png");
        width: 100%;
        height: 725px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 20px;
    }

    .bg-event {
        height: 632px;

        background-image: url("./image/DSC_2363.JPG");
        background-size: cover;
        background-position: center;
        position: relative;
    }
}
